<template>
  <!-- process step -->
  <div class="check-step">
    <ul>
      <li :class="{cur:address}"><span></span></li>
      <li :class="{cur:order}"><span></span></li>
      <li :class="{cur:payment}"><span></span></li>
      <li :class="{cur:confirmation}"><span></span></li>
    </ul>
  </div>
</template>

<script>
  export default {
    props:['address','order','payment','confirmation']
  }
</script>



