<template>
  <div id="body">
  <div id="wrap">
    <div class="contain">
      <input id="tabUp" class="tabUp" name="tab" type="radio" checked>
      <label class="tab" for="tabUp"></label>
      <input id="tabIn" class="tabIn" name="tab" type="radio">
      <label class="tab" for="tabIn"></label>
      <div class="formContain">
        <section class="signUp">
          <form name="signUp">
            <div><label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="username" name="username" type="text"></label>
              <span class='nameError'>{{nameError}}</span>
            </div>
            <div><label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="email" name="email" type="email"></label>
              <span class='emailError'>{{emailError}}</span>
            </div>
            <div><label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="password"  name="password" type="password"></label>
              <span class='passwordError'>{{passwordError}}</span>
            </div>
            <div><label><input name="confire_password" v-model="confire_password" type="password"></label>
              <span class='confire_passwordError'>{{confire_passwordError}}</span>
            </div>
            <div><input @click.prevent="signUp()" type="submit" value=""></div>
            <div class="hr"></div>
            <div class="footLink">
              <label for="tabIn"></label>
            </div>
          </form>
        </section>
        <section class="login">
          <form name="login">
            <div><label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="username" name="username" type="text"></label>
              <span class='nameError'>{{nameError}}</span>
            </div>
            <div><label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="password" name="password" type="password"></label>
              <span class='passwordError'>{{passwordError}}</span>
            </div>
            <div><input @click.prevent="login()" type="submit" value=""></div>
            <div class="hr"></div>
            <div class="footLink">
              <a href="#forget"></a>
            </div>
          </form>
        </section>
      </div>
    </div>
  </div>
  </div>
</template>

<style scoped>
  #body {
    width:100vw;
    height:100vh;
    margin: 0;
    color: rgba(243, 181, 155, 0.63)!important;
    background: #c8c8c8;
    font: 600 16px/18px 'Open Sans',sans-serif;
    background: url("./../../static/images/color.jpeg")!important;
  }
</style>

<script>
import './../../static/css/login.css'

import AV from 'leancloud-storage'

  export default {
    data(){
      return {
        username: '',
        email:'',
        password: '',
        nameError:'',
        emailError:'',
        passwordError:'',
        confire_passwordError:'',
        confire_password: ''
      }
    },
    methods:{
      closeHint(){
        setTimeout(()=>{
          this.nameError = ""
          this.emailError = ""
          this.passwordError = ""
          this.confire_passwordError = ""
        },2000)
      },
      checkForm(isEmail){ //
        let username,password,email

        username = /.{2,}/.test(this.username)
        password = /\w{4,}/.test(this.password)

        if(!arguments[0]){
          email = /\w+@\w+\.\w+/.test(this.email)

          //
          if(!this.confire_password){
            this.confire_passwordError = ''
            this.closeHint()
            return false
          }
          if(this.confire_password!=this.password){
            this.confire_passwordError = ''
            this.closeHint()
            return false
          }

        }


        if(!username){
          this.nameError = ''
          this.closeHint()
          return false
        }
        if(email===false){ //
          this.emailError = ''
          this.closeHint()
          return false
        }

        if(!password){
          this.passwordError = '4'
          this.closeHint()
          return false
        }


        return true
      },
      signUp(){
        let checkResult = this.checkForm()
        if(!checkResult){
          return
        }

        let user = new AV.User()
        user.setUsername(this.username)
        user.setPassword(this.password)
        user.setEmail(this.email)
        user.signUp().then((loginedUser)=>{
          document.location.hash = '#/goodslist'
        },(err)=>{
          console.log('err',err)
          if(err.code===125){
            this.emailError = ''
          }
          if(err.code===202){
            this.nameError = ''
          }
          if(err.code===203){
            this.emailError = ''
          }
          this.closeHint()
        })
      },
      login(){
        let checkResult = this.checkForm('email')
        if(!checkResult){
          console.log('')
          return
        }

        AV.User.logIn(this.username, this.password).then(function (loginedUser) {
          //console.log('loginedUser login',loginedUser);
          //
          document.location.hash = '#/goodslist'
        }, (err)=>{
          console.log('err',err)
          if(err.code===210){
            console.log('err2')
            this.passwordError = ''
          }
          if(err.code===211){
            this.nameError = ''
          }
        });
      }
    }
  }
</script>




